import React, { Component } from 'react'
import { 
  BrowserRouter as Router, 
  Route, 
  Switch
} from 'react-router-dom'
import './App.css'
import EXAppNavbar from './components/layout/AppNavbar'
import EXAppFooter from './components/layout/AppFooter'
import EXArticle from './components/article/Article'
import EXTools from './components/tools/Tools'
import EXStore from './components/store/Store'
import EXLogin from './components/auth/Login'
import EXRegister from './components/auth/Register'
import { createStore, applyMiddleware } from 'redux'
import { Provider } from 'react-redux'
import thunk from 'redux-thunk'
import reducer from './store/reducer'

const store = createStore(
  reducer,
  applyMiddleware(thunk)
)

class App extends Component {
  constructor(props) {
    super(props)

    this.state = { 
      width: 0, 
      height: 0
    }

    this.updateWindowDimensions = this.updateWindowDimensions.bind(this)
  }

  componentDidMount() {
    this.updateWindowDimensions()
    window.addEventListener('resize', this.updateWindowDimensions)
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions)
  }
  
  updateWindowDimensions() {
    this.setState({ 
      width: window.innerWidth, 
      height: window.innerHeight 
    })
  }

  render() {
    const mobile = (this.state.width <= 768)? true : false

    return (
      <Provider 
        store={store}
      >
        <Router>
          <div className="ex-app">
            <EXAppNavbar 
              mobile={mobile}
            />
            
            <div className="ex-content">
              <Switch>
                <Route
                  exact
                  path="/"
                  component={
                    (props) => <EXArticle 
                      {...props} 
                      mobile={mobile} 
                    />
                  }
                />
                <Route
                  exact
                  path="/article"
                  component={
                    (props) => <EXArticle 
                      {...props} 
                      mobile={mobile} 
                    />
                  }
                />
                <Route
                  exact
                  path="/tools"
                  component={
                    (props) => <EXTools 
                      {...props} 
                      mobile={mobile} 
                    />
                  }
                />
                <Route
                  exact
                  path="/store"
                  component={
                    (props) => <EXStore 
                      {...props} 
                      mobile={mobile} 
                    />
                  }
                />
                <Route
                  exact
                  path="/login"
                  component={
                    (props) => <EXLogin 
                      {...props} 
                      mobile={mobile} 
                    />
                  }
                />
                <Route
                  exact
                  path="/register"
                  component={
                    (props) => <EXRegister 
                      {...props} 
                      mobile={mobile} 
                    />
                  }
                />
              </Switch>
            </div>

            <EXAppFooter />
          </div>
        </Router>
      </Provider>
    )
  }
}

export default App
