import React, { Component } from 'react'
import debounce from 'lodash.debounce'
import moment from 'moment'
import { connect } from 'react-redux'
import { fetchListHeadline } from '../../store/action/headline'
import { fetchListTag } from '../../store/action/tag'
import { fetchListArticleByTag } from '../../store/action/article'
import {
  BrowserRouter as Router,
  Link
} from 'react-router-dom'
import {
  Container,
  ListGroup,
  Image,
  Nav
} from 'react-bootstrap'
import EXBanner from '../banner/Banner'
import { EXInputSearch, EXIcon, EXLoading, EXLoaAll } from '../form'
import env from '../../store/env'
import 'moment/locale/id'
moment.localeData('id')

class EXArticle extends Component {
  _isMounted = false

  constructor(props) {
    super(props)

    this.state = {
      articles: [],
      currentTag: '',
      limit: 10,
      page: 1,
      lastPage: 1
    }

    // Binds our scroll event handler
    window.onscroll = debounce(() => {
      const {
        loadMoreArticles,
        state: {
          page,
          lastPage
        },
      } = this

      if (
        props.errorArticle || 
        props.loadingArticle ||
        page >= lastPage
      ) return

      if (
        window.innerHeight + document.documentElement.scrollTop
        === document.documentElement.offsetHeight
      ) {
        loadMoreArticles()
      }
    }, 100)

    this.handleTagChange = this.handleTagChange.bind(this)
    this.loadMoreArticles = this.loadMoreArticles.bind(this)
  }

  componentDidMount() {
    this._isMounted = true
    this.props.dispatch(fetchListHeadline())
    this.props.dispatch(fetchListTag())
    .then((tags) => {
      this.props.dispatch(fetchListArticleByTag(tags[0], this.state.page, this.state.limit))
      .then((article) => {
        if(this._isMounted) {
          this.handleSetupPage(tags[0], article)
        }
      })
    })
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  handleSetupPage(currentTag, article) {
    this.setState({
      articles: article.data,
      currentTag: currentTag,
      page: article.current_page,
      lastPage: article.last_page
    })
  }

  handleTagChange(tag) {
    this.props.dispatch(fetchListArticleByTag(tag, 1, this.state.limit))
    .then((article) => {
      this.setState({
        articles: article.data,
        currentTag: tag,
        page: 1,
        lastPage: 1
      })
    })
  }

  loadMoreArticles() {
    this.props.dispatch(fetchListArticleByTag(this.state.currentTag, (this.state.page + 1), this.state.limit))
    .then((article) => {
      this.setState({
        articles: [
          ...this.state.articles,
          ...article.data
        ],
        page: article.current_page,
        lastPage: article.last_page
      })
    })
  }

  isTagActive(tag) {
    if(this.state.currentTag === tag) return true
    return false
  }

  dateFormat(value) {
    return moment(value).format('DD MMMM YYYY')
  }

  render() {
    const mobile = this.props.mobile
    const { 
      errorHeadline, 
      loadingHeadline, 
      headlines,
      errorTag, 
      loadingTag, 
      tags,
      errorArticle, 
      loadingArticle
    } = this.props

    let exContainer = {
      maxWidth: '62.4rem',
      padding: '0rem 2rem',
      margin: '0rem auto'
    }

    let exBody = {
      margin: '0rem auto',
      position: 'relative',
      padding: '0.8rem 2rem 2rem'
    }

    let exListGroup = {
      marginTop: '4rem',
      boxShadow: 'rgba(0, 0, 0, 0.133) 0px 0.16rem 0.36rem 0px, rgba(0, 0, 0, 0.11) 0px 0.03rem 0.09rem 0px'
    }

    const exListGroupItem = {
      padding: '.75rem 0rem'
    }

    let exListGroupNav = {
      padding: '0rem',
      background: '#F9F9F9',
      overflowY: 'auto'
    }

    let exContainerSeacrh = {
      maxWidth: '50%'
    }

    const exShowAll = {
      fontSize: '1rem',
      textAlign: 'center',
      lineHeight: '1',
      whiteSpace: 'normal',
      WebkitFontSmoothing: 'antialiased',
      color: 'rgb(153, 153, 153)',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontWeight: '400'
    }

    const exArticle = {
      width: '100%',
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      flex: '1 1 0%',
      paddingLeft: '1rem',
      paddingRight: '1rem',
      background: '#FFFFFF'
    }

    const exArticleItem = {
      display: 'flex',
      flexDirection: 'row',
      WebkitBoxPack: 'justify',
      justifyContent: 'space-between'
    }

    const exArticleItemContent = {
      display: 'flex',
      flexDirection: 'column',
      flex: '1 1 0%'
    }

    const exArticleTitle = {
      textAlign: 'left',
      whiteSpace: 'normal'
    }

    const exTitle = {
      WebkitFontSmoothing: 'antialiased',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '1rem',
      fontWeight: '600',
      color: 'rgb(0, 0, 0)'
    }

    const exUserBox = {
      marginTop: '0.2rem',
      display: 'flex',
      WebkitBoxAlign: 'center',
      alignItems: 'center'
    }

    const exArticleItemImage = {
      paddingRight: '1.2rem'
    }

    const exCaptionBox = {
      marginTop: '0.2rem',
      display: 'flex',
      WebkitBoxAlign: 'center',
      alignItems: 'center'
    }

    const exCaptionAction = {
      height: '28px',
      paddingRight: '10px',
      lineHeight: '28px'
    }

    const exCaptionActionDate = {
      WebkitFontSmoothing: 'antialiased',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '0.8rem',
      fontWeight: '400',
      color: 'rgb(153, 153, 153)'
    }

    let exNav = {
      margin: 'auto',
      width: '68rem'
    }
    
    let exNavItem = {
      width: 'auto'
    }

    const exNavLink = {
      WebkitFontSmoothing: 'antialiased',
      color: 'rgb(61, 61, 61)',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '1rem',
      fontWeight: '600',
      textTransform: 'capitalize',
      display: 'inline-block',
      position: 'relative'
    }

    const exNavLinkActive = {
      position: 'absolute',
      height: '0.2rem',
      bottom: '0rem',
      right: '0.7rem',
      left: '0.7rem',
      background: '#F5B41C'
    }

    if(mobile) {
      exContainer.padding = '0rem 0.2rem'
      exBody.padding = '0.8rem 0rem 0rem'
      exListGroup.marginTop = '0.5rem'
      exListGroup.boxShadow = 'none'
      exContainerSeacrh.maxWidth = '100%'
      exListGroupNav.padding = '0rem'
      exNavItem.width = 'auto'
    }

    return (
      <Container
        style={exContainer}
      >
        <div 
          style={exBody}
        >
          <EXBanner 
            mobile={mobile}
            headlines={headlines}
            loading={loadingHeadline}
            error={errorHeadline}
            url={env.IMAGE_URL}
          />

          <ListGroup 
            variant="flush"
            style={exListGroup}
          >
            <ListGroup.Item>
              <Container
                style={exContainerSeacrh}
              >
                <EXInputSearch 
                  placeholder={''}
                />
              </Container>
            </ListGroup.Item>
            <ListGroup.Item
              style={exListGroupNav}
            >
              <Nav
                className={'justify-content-left'}
                style={exNav}
              >
                {
                  (loadingTag || errorTag)? 
                    <EXLoading
                      position={'left'}
                    />
                  : tags.map((tag, n) => {
                    return (
                      <div 
                        key={n}
                        style={exNavItem}
                        className="text-left"
                      >
                        <Nav.Item>
                          <Nav.Link
                            style={exNavLink}
                            onClick={() => this.handleTagChange(tag)}
                          >
                            {tag}
                            {
                              (this.isTagActive(tag))?
                                <div style={exNavLinkActive}></div>
                              : <></>
                            }
                          </Nav.Link>
                        </Nav.Item>
                      </div>
                    )
                  })
                }
              </Nav>
            </ListGroup.Item>

            <div
              style={exArticle}
            >
              {
                (loadingArticle || errorArticle)? 
                  <EXLoading />
                : this.state.articles.map((article, index) => {
                  return (
                    <ListGroup.Item
                      style={exListGroupItem}
                      key={index}
                    >
                      <div
                        style={exArticleItem}
                      >
                        <Router>
                          <div 
                            style={exArticleItemImage}
                          >
                            <Link 
                              to="/home"
                            >
                              <Image 
                                src={`${env.IMAGE_URL}${article.thumbnail}`} 
                                rounded
                                width={80}
                                height={80}
                              />
                            </Link>
                          </div>
                          <div 
                            style={exArticleItemContent}
                          >
                            <div 
                              style={exArticleTitle}
                            >
                              <Link 
                                to="/home"
                              >
                                <span
                                  style={exTitle} 
                                >
                                  {article.title}
                                </span>
                              </Link>
                              <div
                                style={exUserBox}
                              >
                                <span
                                  style={exCaptionActionDate}
                                >{this.dateFormat(article.created_at)}</span>
                              </div>
                              <div 
                                style={exCaptionBox}
                              >
                                <div
                                  style={exCaptionAction}
                                >
                                  <Link 
                                    to="/user"
                                    style={exCaptionBox}
                                  >
                                    <EXIcon
                                      type={'chat'}
                                      size={28}
                                      color={'rgb(153, 153, 153)'}
                                    />
                                  </Link>
                                </div>
                                <div
                                  style={exCaptionAction}
                                >
                                  <Link 
                                    to="/user"
                                    style={exCaptionBox}
                                  >
                                    <EXIcon
                                      type={'option'}
                                      size={28}
                                      color={'rgb(153, 153, 153)'}
                                    />
                                  </Link>
                                </div>
                              </div>
                            </div>
                          </div>
                        </Router>
                      </div>
                    </ListGroup.Item>
                  )
                })
              }
            </div>
            <ListGroup.Item
              disabled
              style={exShowAll}
            >
              {
                (loadingArticle || errorArticle)? 
                  <EXLoading />
                : <EXLoaAll />
              }
            </ListGroup.Item>
          </ListGroup>
        </div>
      </Container>
    )
  }
}

const mapStateToProps = state => ({
  headlines: state.headline.items,
  loadingHeadline: state.headline.loading,
  errorHeadline: state.headline.error,
  tags: state.tag.items,
  loadingTag: state.tag.loading,
  errorTag: state.tag.error,
  loadingArticle: state.article.loading,
  errorArticle: state.article.error
})

export default connect(mapStateToProps)(EXArticle)