import env from '../env'

function listHeadline() {
  return fetch(`${env.BASE_URL}article/headline`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    })
    .then(handleErrors)
    .then(res => res.json())
}

export function fetchListHeadline() {
  return dispatch => {
    dispatch(fetchHeadlineBegin())
    return listHeadline()
      .then(json => {
        dispatch(fetchHeadlineSuccess(json.articles.data))
        return json.articles.data
      })
      .catch(error =>
        dispatch(fetchHeadlineFailure(error))
      )
  }
}

function handleErrors(response) {
  if (!response.ok) {
    throw Error(response.statusText)
  }
  return response
}

export const FETCH_HEADLINE_BEGIN = 'FETCH_HEADLINE_BEGIN'
export const FETCH_HEADLINE_SUCCESS = 'FETCH_HEADLINE_SUCCESS'
export const FETCH_HEADLINE_FAILURE = 'FETCH_HEADLINE_FAILURE'

export const fetchHeadlineBegin = () => ({
  type: FETCH_HEADLINE_BEGIN
})

export const fetchHeadlineSuccess = articles => ({
  type: FETCH_HEADLINE_SUCCESS,
  payload: { articles }
})

export const fetchHeadlineFailure = error => ({
  type: FETCH_HEADLINE_FAILURE,
  payload: { error }
})
