import {
  FETCH_TAG_BEGIN,
  FETCH_TAG_SUCCESS,
  FETCH_TAG_FAILURE
} from '../action/tag'

const initialState = {
  items: [],
  loading: false,
  error: null
};

export default function headlineReducer(
  state = initialState,
  action
) {
  switch (action.type) {
    case FETCH_TAG_BEGIN:
      return {
        ...state,
        loading: true,
        error: null
      }

    case FETCH_TAG_SUCCESS:
      return {
        ...state,
        loading: false,
        items: action.payload.tags
      }

    case FETCH_TAG_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
        items: []
      }

    default:
      return state
  }
}
