import React, { Component } from 'react'
import {
  Button
} from 'react-bootstrap'
import { 
  Bell, 
  Person,
  Outlet,
  X,
  List
} from 'react-bootstrap-icons'

class EXIconButton extends Component {
  render() {
    const exButton = {
      padding: '0rem'
    }

    const icons = {
      outlet: Outlet,
      bell: Bell,
      person: Person,
      x: X,
      list: List
    }

    const Icon = icons[this.props.type || 'outlet']
    const size = this.props.size || 32
    const color = this.props.color || '#1E1E1E'

    return (
      <Button 
        variant="link"
        style={exButton}
        onClick={this.props.onClick}
      >
        <Icon
          size={size}
          color={color}
        />
      </Button>
    )
  }
}

export default EXIconButton