import env from '../env'

function listTag() {
  return fetch(`${env.BASE_URL}article/list-tag`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    })
    .then(handleErrors)
    .then(res => res.json())
}

export function fetchListTag() {
  return dispatch => {
    dispatch(fetchTagBegin())
    return listTag()
      .then(json => {
        dispatch(fetchTagSuccess(json.data))
        return json.data
      })
      .catch(error =>
        dispatch(fetchTagFailure(error))
      )
  }
}

function handleErrors(response) {
  if (!response.ok) {
    throw Error(response.statusText)
  }
  return response
}

export const FETCH_TAG_BEGIN = 'FETCH_TAG_BEGIN'
export const FETCH_TAG_SUCCESS = 'FETCH_TAG_SUCCESS'
export const FETCH_TAG_FAILURE = 'FETCH_TAG_FAILURE'

export const fetchTagBegin = () => ({
  type: FETCH_TAG_BEGIN
})

export const fetchTagSuccess = tags => ({
  type: FETCH_TAG_SUCCESS,
  payload: { tags }
})

export const fetchTagFailure = error => ({
  type: FETCH_TAG_FAILURE,
  payload: { error }
})
