import React, { Component } from 'react'
import { 
  InputGroup,
  FormControl
} from 'react-bootstrap'
import { EXIcon } from './index'

class EXInput extends Component {
  constructor(props) {
    super(props)

    this.state = {
      focus: false
    }

    this.toggleFocus = this.toggleFocus.bind(this)
  }

  toggleFocus() {
    this.setState({ focus: true })
  }

  render() {
    const props = this.props
    const exPrepend = {
      border: 'none',
      color: '#8E8E8E',
      padding: '.345rem .45rem',
      borderRadius: '.600rem 0rem 0rem .600rem'
    }

    let exInput = {
      border: 'none',
      background: 'rgb(234, 235, 236)',
      borderRadius: '0rem .600rem .600rem 0rem',
      padding: '.375rem 0rem'
    }

    if (this.state.focus) {
      exInput.boxShadow = 'none'
    }

    return (
      <InputGroup>
        <InputGroup.Prepend>
          <InputGroup.Text
            style={exPrepend}
          >
            <EXIcon 
              size={26}
              type={'search'}
              color={'#8E8E8E'}
            />
          </InputGroup.Text>
        </InputGroup.Prepend>
        <FormControl
          size="md" 
          type="text"
          placeholder={props.placeholder}
          onFocus={this.toggleFocus}
          style={exInput}
        />
      </InputGroup>
    )
  }
}

export default EXInput