import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { 
  Navbar,
  Container,
  Nav
} from 'react-bootstrap'
import { EXIconButton } from '../form'
import EXAppSideBar from './AppSideBar'

class EXAppNavbar extends Component {
  constructor(props) {
    super(props)

    this.state = {
      sideMenu: false,
      menus: [
        {
          name: 'Artikel',
          to: '/article',
          key: 0,
          class: 'text-left',
          active: true
        },
        {
          name: 'Tools',
          to: '/tools',
          key: 1,
          class: 'text-center',
          active: false
        },
        {
          name: 'Store',
          to: '/store',
          key: 2,
          class: 'text-right',
          active: false
        }
      ]
    }

    this.toogleSideMenu = this.toogleSideMenu.bind(this)
    this.toogleActiveNav = this.toogleActiveNav.bind(this)
  }

  toogleSideMenu() {
    this.setState({ 
      sideMenu: !this.state.sideMenu
    })
  }

  exNavbarCollapse(mobile, exIcon) {
    if(mobile) {
      return (
        <div>
          <EXIconButton 
            type={'bell'}
            size={32}
          />
        </div>
      )
    }

    return (
      <>
        <div style={exIcon}>
          <EXIconButton 
            type={'bell'}
            size={32}
          />
        </div>
        <div style={exIcon}>
          <Link
            to={'/login'}
          >
            <EXIconButton 
              type={'person'}
              size={32}
              onClick={() => this.toogleActiveNav(3)}
            />
          </Link>
        </div>
      </>
    )
  }

  toogleActiveNav(key) {
    const menus = this.state.menus.map((item, i) => {
      if (Number(key) === i) {
        item.active = true
        return item
      }

      item.active = false
      return item
    })

    this.setState({
      menus: menus
    })
  }

  render() {
    const mobile = this.props.mobile

    const exNavbar = {
      display: 'block',
      padding: '0.5rem 1rem 0.01rem 1rem',
      boxShadow: '0 0.4rem 1.2rem 0 rgba(0, 0, 0, 0.05)'
    }

    const exBrand = {
      flexGrow: '1',
      textAlign: 'center',
      marginRight: '0rem'
    }

    const exContainer = {
      maxWidth: '59.4rem'
    }

    const exIcon = {
      paddingRight: '2rem'
    }

    let exNav = {
      maxWidth: '42.4rem',
      margin: 'auto'
    }

    const exNavItem = {
      width: '33.3333333333%'
    }

    let exNavLink = {
      WebkitFontSmoothing: 'antialiased',
      color: 'rgb(0, 0, 0)',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '1.2rem',
      fontWeight: '700',
      textTransform: 'capitalize',
      display: 'inline-block',
      position: 'relative'
    }

    const exNavLinkActive = {
      position: 'absolute',
      height: '0.3rem',
      bottom: '0rem',
      right: '0.1rem',
      left: '0.1rem',
      background: '#F5B41C'
    }

    const exBrandSpacer = {
      width: '8.25rem'
    }

    if(mobile) {
      exNavLink.fontSize = '1rem'
      exNavLink.fontWeight = '500'
    }

    return (
      <>
      {
        (this.state.sideMenu)?
          <EXAppSideBar
            onClick={this.toogleSideMenu}
          />
        : <></>
      }

      <Navbar
        sticky="top"
        variant="light" 
        bg="white"
        style={exNavbar}
      >
        <Container
          style={exContainer}
        > 
          {
            (mobile)?
              <EXIconButton 
                type={'list'}
                size={32}
                onClick={this.toogleSideMenu}
              />
            : 
              <div 
                style={exBrandSpacer}
              ></div>
          }

          <Navbar.Brand 
            to={'/article'}
            as={Link}
            style={exBrand}
            onClick={() => this.toogleActiveNav(0)}
          >
            <img
              src={process.env.PUBLIC_URL + '/logo_horizontal.svg'}
              alt="PKNSTAN LOGO"
              height={(mobile)? 30 : 40}
            />
          </Navbar.Brand>

          { this.exNavbarCollapse(mobile, exIcon) }
        </Container>
        
        <Nav
          className="justify-content-center"
          style={exNav}
          onSelect={this.toogleActiveNav}
        >
          {
            this.state.menus.map((menu, index) => {
              return (
                <div 
                  style={exNavItem}
                  className={menu.class}
                  key={index}
                >
                  <Nav.Item>
                    <Nav.Link
                      style={exNavLink}
                      eventKey={menu.key}
                      to={menu.to}
                      as={Link}
                    >
                      {menu.name}
                      {
                        (menu.active)?
                        <div style={exNavLinkActive}></div>
                        : <></>
                      }
                    </Nav.Link>
                  </Nav.Item>
                </div>
              )
            })
          }
        </Nav>
      </Navbar>
      </>
    )
  }
}

export default EXAppNavbar