import React, { Component } from 'react'
import moment from 'moment'
import {
  Carousel,
  Row,
  Col
} from 'react-bootstrap'
import EXNextSlide from './NextSlide'
import EXActiveSlide from './ActiveSlide'
import './Banner.css'
import { EXLoading } from '../form'
import 'moment/locale/id'
moment.localeData('id')

class EXBanner extends Component {
  constructor(props) {
    super(props)

    this.state = {
      hoverCaptionTitle: false,
      hoverCaptionTitleNext: false
    }

    this.hoverTitle = this.hoverTitle.bind(this)
    this.hoverTitleNext = this.hoverTitleNext.bind(this)
  }

  hoverTitle() {
    this.setState({
      hoverCaptionTitle: !this.state.hoverCaptionTitle
    })
  }

  hoverTitleNext() {
    this.setState({
      hoverCaptionTitleNext: !this.state.hoverCaptionTitleNext
    })
  }

  handleNextImage(index) {
    const length = this.props.headlines.length
    if(index < (length - 1)) {
      return this.props.headlines[index + 1]
    }

    return this.props.headlines[0]
  }

  dateFormat(value) {
    return moment(value).format('DD MMMM YYYY')
  }

  render() {
    const mobile = this.props.mobile
    const hoverCaptionTitle = (this.state.hoverCaptionTitle)? true : false
    const hoverCaptionTitleNext = (this.state.hoverCaptionTitleNext)? true : false

    let exColSlide = {
      paddingRight: '0px'
    }

    let exCaption = {
      position: 'absolute',
      right: '0',
      bottom: '0',
      left: '15px',
      zIndex: '10',
      paddingTop: '0.7rem',
      paddingBottom: '20px',
      paddingRight: '0.7rem',
      paddingLeft: '0.7rem',
      color: '#FFFFFF',
      background: 'linear-gradient(transparent 0%, rgba(0, 0, 0, 0.6) 40%, rgba(0, 0, 0, 0.8) 100%)',
      borderRadius: '0rem 0rem 0.5rem 0.5rem'
    }

    const exCaptionTitle = {
      textAlign: 'left',
      lineHeight: '1.25',
      whiteSpace: 'normal'
    }

    let exTitle = {
      WebkitFontSmoothing: 'antialiased',
      color: 'rgb(255, 255, 255)',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '1rem',
      fontWeight: '600'
    }

    let exTitleNext = {
      WebkitFontSmoothing: 'antialiased',
      color: 'rgb(255, 255, 255)',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '1rem',
      fontWeight: '600'
    }

    if(hoverCaptionTitle) exTitle.color = '#0097A7'
    if(hoverCaptionTitleNext) exTitleNext.color = '#0097A7'

    const exBoxHeadline = {
      display: 'flex',
      flexDirection: 'row'
    }

    const exHeadline = {
      WebkitFontSmoothing: 'antialiased',
      color: '#F5B41C',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '0.8rem',
      fontWeight: '800'
    }

    const exCaptionBox = {
      marginTop: '0.2rem',
      display: 'flex',
      WebkitBoxAlign: 'center',
      alignItems: 'center'
    }

    const exCaptionAction = {
      height: '30px',
      paddingRight: '10px',
      lineHeight: '30px'
    }

    const exCaptionActionDate = {
      WebkitFontSmoothing: 'antialiased',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif',
      fontSize: '0.8rem',
      fontWeight: '400',
      color: 'rgb(153, 153, 153)'
    }

    if(mobile) {
      exColSlide.paddingRight = '15px'
      exCaption.right = '15px'
    }

    if(this.props.loading || this.props.error) {
      return (
        <EXLoading />
      )
    }

    return (
      <Carousel
        className="ex-carousel"
      >
        {
          this.props.headlines.map((headline, index) => {
            return (
              <Carousel.Item
                key={index}
              >
                <Row>
                  <Col 
                    xs={12} sm={12} md={9} lg={9}
                    style={exColSlide}
                  >
                    <EXActiveSlide
                      url={this.props.url}
                      headline={headline}
                      exCaption={exCaption}
                      exCaptionTitle={exCaptionTitle}
                      exTitle={exTitle}
                      hoverTitle={this.hoverTitle}
                      exBoxHeadline={exBoxHeadline}
                      exCaptionBox={exCaptionBox}
                      exHeadline={exHeadline}
                      exCaptionAction={exCaptionAction}
                      exCaptionActionDate={exCaptionActionDate}
                      dateFormat={this.dateFormat}
                    />
                  </Col>
                  {
                    (!mobile)?
                      <Col 
                        sm={3}
                        style={exColSlide}
                      >
                        <EXNextSlide
                          url={this.props.url}
                          headline={this.handleNextImage(index)}
                          exCaption={exCaption}
                          exCaptionTitle={exCaptionTitle}
                          exTitleNext={exTitleNext}
                          hoverTitleNext={this.hoverTitleNext}
                          exBoxHeadline={exBoxHeadline}
                          exCaptionBox={exCaptionBox}
                          exHeadline={exHeadline}
                          exCaptionAction={exCaptionAction}
                          exCaptionActionDate={exCaptionActionDate}
                          dateFormat={this.dateFormat}
                        />
                      </Col>
                    : <></>
                  }
                </Row>
              </Carousel.Item>
            )
          })
        }
      </Carousel>
    )
  }
}

export default EXBanner