import React, { Component } from 'react'
import { 
  Spinner
} from 'react-bootstrap'

class EXLoading extends Component {
  render() {
    const position = this.props.position || 'center'

    const exLoading = {
      width: '100%',
      textAlign: position,
      color: 'rgb(85, 153, 211)'
    }

    return (
      <div
        style={exLoading}
      >
        <Spinner animation="grow" size="sm" />
        <Spinner animation="grow" size="sm" />
        <Spinner animation="grow" size="sm" />
      </div>
    )
  }
}

export default EXLoading