import {
  FETCH_ARTICLE_BEGIN,
  FETCH_ARTICLE_SUCCESS,
  FETCH_ARTICLE_FAILURE
} from '../action/article'

const initialState = {
  items: [],
  loading: false,
  error: null
};

export default function headlineReducer(
  state = initialState,
  action
) {
  switch (action.type) {
    case FETCH_ARTICLE_BEGIN:
      return {
        ...state,
        loading: true,
        error: null
      }

    case FETCH_ARTICLE_SUCCESS:
      return {
        ...state,
        loading: false,
        items: action.payload.articles
      }

    case FETCH_ARTICLE_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
        items: []
      }

    default:
      return state
  }
}
