import React, { Component } from 'react'
import { 
  Bell, 
  Person,
  Outlet,
  BoxArrowRight,
  Chat,
  Heart,
  Option,
  Search,
  CheckCircle
} from 'react-bootstrap-icons'

class EXIcon extends Component {
  render() {
    const icons = {
      outlet: Outlet,
      bell: Bell,
      person: Person,
      box_arrow_right: BoxArrowRight,
      chat: Chat,
      heart: Heart,
      option: Option,
      search: Search,
      check_circle: CheckCircle
    }

    const Icon = icons[this.props.type || 'outlet']
    const size = this.props.size || 32
    const color = this.props.color || '#1E1E1E'

    return (
      <Icon
        size={size}
        color={color}
      />
    )
  }
}

export default EXIcon