import env from '../env'

function listArticleByTag(tag, page, limit) {
  return fetch(`${env.BASE_URL}article/headline?tag=${tag}&page=${page}&limit=${limit}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    })
    .then(handleErrors)
    .then(res => res.json())
}

export function fetchListArticleByTag(tag, page, limit) {
  return dispatch => {
    dispatch(fetchArticleBegin())
    return listArticleByTag(tag, page, limit)
      .then(json => {
        dispatch(fetchArticleSuccess(json.articles.data))
        return json.articles
      })
      .catch(error =>
        dispatch(fetchArticleFailure(error))
      )
  }
}

function handleErrors(response) {
  if (!response.ok) {
    throw Error(response.statusText)
  }
  return response
}

export const FETCH_ARTICLE_BEGIN = 'FETCH_ARTICLE_BEGIN'
export const FETCH_ARTICLE_SUCCESS = 'FETCH_ARTICLE_SUCCESS'
export const FETCH_ARTICLE_FAILURE = 'FETCH_ARTICLE_FAILURE'

export const fetchArticleBegin = () => ({
  type: FETCH_ARTICLE_BEGIN
})

export const fetchArticleSuccess = articles => ({
  type: FETCH_ARTICLE_SUCCESS,
  payload: { articles }
})

export const fetchArticleFailure = error => ({
  type: FETCH_ARTICLE_FAILURE,
  payload: { error }
})
