import {
  FETCH_HEADLINE_BEGIN,
  FETCH_HEADLINE_SUCCESS,
  FETCH_HEADLINE_FAILURE
} from '../action/headline'

const initialState = {
  items: [],
  loading: false,
  error: null
};

export default function headlineReducer(
  state = initialState,
  action
) {
  switch (action.type) {
    case FETCH_HEADLINE_BEGIN:
      return {
        ...state,
        loading: true,
        error: null
      }

    case FETCH_HEADLINE_SUCCESS:
      return {
        ...state,
        loading: false,
        items: action.payload.articles
      }

    case FETCH_HEADLINE_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
        items: []
      }

    default:
      return state
  }
}
